.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

h1{
  font-family: 'Bigelow Rules', cursive;
  font-size: 72px;
  color: #0ccac4
}


.box-container{
    display: block;
    width: 600px;
    height: 400px;
    margin: 50px;
    background-color: floralwhite;
    text-align: center;
}